import React, { useState, useEffect } from "react";
import { dbService } from "../fbase";
import { useHistory } from "react-router-dom";

const Quiz = ({typeObj, setTypeObj}) => {
    let history = useHistory();

    const[button1, setButton1] = useState("");
    const[button2, setButton2] = useState("");

    const[question1, setQuestion1] = useState(null);
    const[question2, setQuestion2] = useState(null);

    useEffect(() => {
        if (!typeObj) {
            setTypeObj({
                typeState: 'IE',
                questionNo: 1,
                IE: 0,
                NS: 0,
                FT: 0,
                PJ: 0,
            });
        }

        console.log(typeObj);

        dbService.collection("questions")
            //.where("quizNo", "==", typeObj.questionNo)
            .orderBy("typeNo", "asc")
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    // doc.data() is never undefined for query doc snapshots
                    console.log(doc.id, " => ", doc.data());
                });
                const questionArray = querySnapshot.docs.map((doc) => ({id: doc.id, ...doc.data()}))
                console.log(questionArray)
                //setQuestions(questionArray)
            })
            .catch((error) => {
                console.log("Error getting documents: ", error);
            });
    }, [])

    const setQuestions = (questionArray) => {
        setQuestion1(questionArray[0]);
        setQuestion2(questionArray[1]);

        setButton1(question1.text);
        setButton2(question2.text);
    }

    const onBtnClick = async (event) => {
        if (typeObj.questionNo >= 20) {
            history.push('/result')
        }

        let questionObj = event.target.name == 'question1' ? question1 : question2;

        let qNo = typeObj.questionNo + 1;
        let typeNm = typeObj.type;
        let typePoint = typeObj[`${typeNm}`] + questionObj.point;
        let typeSt = qNo == 6 ? 'NS' : qNo == 11 ? 'FT' : qNo == 16 ? 'PJ' : typeNm;

        setTypeObj({
            typeState: typeSt,
            questionNo: qNo,
            [typeNm]: typePoint,
        });
        
        history.push('/quiz')
    }

    return (
        <div>
            <h3>Quiz{typeObj ? `${typeObj.quizNo}` : '' }</h3>
            <button onClick={onBtnClick} name="question1">{button1}</button>
            <button onClick={onBtnClick} name="question2">{button2}</button>
        </div>
    )
}

export default Quiz;